class TitleScreen extends Phaser.Scene {
    constructor() {
        super({ key: 'TitleScreen' });
    }

    preload() {
        this.load.image('background', 'assets/title_background.png'); // Ensure this image exists
    }

    create() {
        // Add background
        this.add.image(400, 300, 'background');

        // Add title text
        this.add.text(400, 200, 'Endless Car Game', { fontSize: '48px', fill: '#fff' }).setOrigin(0.5);

        // Add play button
        const playButton = this.add.text(400, 400, 'Play', { fontSize: '32px', fill: '#0f0' }).setOrigin(0.5);
        playButton.setInteractive();

        // On button click, transition to car selection screen
        playButton.on('pointerdown', () => {
            this.scene.start('CarSelectionScreen');  // Go to Car Selection Scene
        });
    }
}

export default TitleScreen;
